import { Avatar, Card, Divider, Flex, Image, Space, Typography } from "antd";
import { IMG_URL } from "./Constans";

const { Title, Paragraph } = Typography;

export function BoardHub() {
    const imgName: string[] = [
        "java.png",
        "spring-boot.png",
        "oracle.png",
        "nodejs.png",
        "react.png",
        "aws_ec2.png",
        "docker.png",
    ];

    return (
        <div style={{ marginBottom: 20 }}>
            <Flex justify="center">
                <Card
                    title={<Title>Board Hub</Title>}
                    bordered={false}
                    style={{ width: "50%", maxWidth: 1000 }}
                    cover={
                        <div>
                            <Flex
                                justify="space-evenly"
                                style={{ marginTop: 10 }}
                            >
                                <Image
                                    src={`${IMG_URL}/gamelist.gif`}
                                    preview={false}
                                    style={{
                                        padding: 3,
                                        maxWidth: 500,
                                        marginRight: 10,
                                    }}
                                />
                                <Flex align="center">
                                    <div>
                                        <Paragraph
                                            strong
                                            style={{ fontSize: 24 }}
                                        >
                                            3  
                                        </Paragraph>
                                        <Paragraph style={{ fontSize: 20 }}>
                                              2
                                        </Paragraph>
                                        <Divider
                                            style={{
                                                border: "black",
                                                fontWeight: "bold",
                                            }}
                                        >
                                            
                                        </Divider>
                                        <Paragraph style={{ fontSize: 20 }}>
                                              OPEN API 
                                        </Paragraph>
                                        <Paragraph style={{ fontSize: 20 }}>
                                               
                                        </Paragraph>
                                    </div>
                                </Flex>
                            </Flex>
                        </div>
                    }
                >
                    <Flex justify="space-between">
                        <Space
                            style={{
                                backgroundColor: "#e7e6e6",
                                padding: 3,
                            }}
                        >
                            {imgName.map((name: string) => (
                                <Image
                                    src={`${IMG_URL}/${name}`}
                                    preview={false}
                                    width={50}
                                />
                            ))}
                        </Space>
                        <Space>
                            <a>
                                <Avatar
                                    size={50}
                                    style={{
                                        backgroundColor: "black",
                                        color: "white",
                                        fontWeight: "bold",
                                    }}
                                >
                                    B
                                </Avatar>
                            </a>
                            <a href="https://github.com/CodeName4885/BoardHub">
                                <Image
                                    src={`${IMG_URL}/github.png`}
                                    preview={false}
                                    width={50}
                                />
                            </a>
                        </Space>
                    </Flex>
                </Card>
            </Flex>
        </div>
    );
}
